.modal-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: rgb(27, 27, 27);
    border: 2px solid #fff;
    background-color: rgba(255, 255, 255, 0.7);
    box-shadow: 0 0 2px 2px rgb(138, 137, 137);
    color:#393F41
  }

  .close-modal-btn{
    margin: 0 auto ;
    text-align: center;
    border: 2px solid rgb(107, 105, 105) ;
    background-color:unset ;
    border-radius: 5px ;
    cursor: pointer ;
    transition: 0.3s ;
    text-transform: uppercase ;
    font-weight: 600 ;
    color:black ;
    padding: 10px;
    transition: 1s;
  }

  .close-modal-btn:hover{
    background-color: rgba(255, 255, 255, 0.9); ;

  }